.btn__blue {
  width: 100%;
  max-width: max-content;
  box-shadow: inset 0 0 26px 0 rgba(255, 255, 255, 0.6);
  background: #5daddc;
  border: none;
  padding: 12px 16px 12px 40px;
  font: 700 14px/1.5 "Manrope", sans-serif;
  color: #fff;
  border-radius: 100px;
  display: grid;
  grid-template-columns: 1fr 44px;
  gap: 24px;
  align-items: center;
  transition: all 0.3s;
  text-align: left;
  z-index: 2;
  position: relative;
}
.btn__blue span {
  width: 44px;
  height: 44px;
  background: linear-gradient(315deg, #7cc7f2 0%, #1e86c2 100%);
  box-shadow: 0px 0px 0px 1px rgba(255, 255, 255, 0.3);
  border-radius: 100%;
