  position: absolute;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  cursor: pointer;
  display: block;
}
.comment-modal__close::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: 20px;
  height: 1px;
  background: #283238;
  transition: all 0.3s;
}
.comment-modal__close::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: 20px;
  height: 1px;
  background: #283238;
  transition: all 0.3s;
}
.comment-modal__close:hover::before, .comment-modal__close:hover::after {
  background: #69c5ec;
}
.comment-modal .comment__slide {
  margin: auto !important;
  height: max-content;
  width: 100%;
  max-width: 1160px;
}
.comment-modal .comment__text {
  -webkit-line-clamp: none;
  line-clamp: none;
  display: block;
